.plans-top {
	.plans-top-image {
		position: relative;
		height: 22vw;
		background-image: url(../img/plan/title-bg.jpg);
		background-size: cover;
		h2 {
			position: absolute;
			top: 50%;
			text-align: center;
			color: #fff;
			width: 100%;
			letter-spacing: 0.24em;
			z-index: 1;
			font-family: fot-tsukuardgothic-std, sans-serif;
			font-weight: 700;
			font-style: bold;
			font-size: 2.8vw;
		}
	}
	.plans-top-image:after {
		content: '';
		background-color: rgba(0,0,0,.13);
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
	}
	@media screen and (max-width: 767px) {
		.plans-top-menu {
			height: auto;
			ul {
				display: block;
			}
		}
		.plans-top-image {
			position: relative;
			height: 30vh;
			img {
				height: 100%;
				width: auto;
			}
			h2 {
				letter-spacing: 0.24em;
				font-size: 5vw;
			}
		}
	}
}