.flow-middle {
	text-align: center;
	.flow-middle-bottom {
		display: flex;
		margin: 0 auto 6vw;
		width: fit-content;
		.flow-middle-img {
			height: fit-content;
			margin: auto 0 0;
		}
	}
	h2 {
		font-size: 2.8vw;
		margin-top: 6.5vw;
		margin-bottom: 3.5vw;
		letter-spacing: 0.24em;
	}
	p {
		font-size: 1.5vw;
		letter-spacing: 9.6px;
		line-height: 60px;
	}
	@media screen and (max-width: 767px) {
		h2 {
			font-size: 5vw;
			line-height: 6vw;
		}
		p {
			text-align: justify;
			font-size: 3.3vw;
			line-height: 4.5vw;
			margin: 0 2%;
		}
		.flow-middle-bottom {
			.flow-middle-img {
				height: fit-content;
				margin: auto 0;
			}
		}
	}
}